import { observer } from 'mobx-react-lite'
import React from 'react'
import { Link } from 'react-router-dom'
import styled from 'styled-components'
import { ROOMS_ROUTE } from '../../utils/const'


const  ThirdStage = observer(() => {
  return ( 
    <Info>
        <Div>
        
<h3><span style={{color:'#f6a617'}}></span><span style={{color:'#f6a617'}}></span></h3>
<ul style={{marginLeft: '15px'}}>
  <li>       .</li>
  <li>      .</li>
  <li>  ,    :      ,       ,      .</li>
</ul>
<br/>
<h3><span style={{color:'#f6a617'}}></span>   .<span style={{color:'#f6a617'}}></span></h3>

   ,    ,        ,        
<br/>
<br/>
        </Div>
<Link to={ROOMS_ROUTE}><SwimBut></SwimBut></Link>
    </Info>
  )
})

const Info = styled.div`
    line-height: 22px;
    flex: 1 1 auto;
    display: flex;
    flex-direction: column;
`
const SwimBut = styled.div`
line-height: normal;
background-color: #f6a617;
padding: 5px;
border-radius: 50%;
justify-content: center;
align-items: center;
font-size: 13pt;
cursor: pointer;
display: flex;
z-index: 2;
`
const Div = styled.div`
display: flex;
    flex-direction: column;
    //flex: 1 1 auto;
`
export default ThirdStage; 
