import React, { useState, useEffect, useContext } from 'react';
import { Link } from 'react-router-dom';
import { Context } from '../..';
import { ROOMS_ROUTE } from '../../utils/const';
import '../../App.css';

const ProgressBar = ({ level, text }) => {
  const [progressWidth, setProgressWidth] = useState(0);
  const { user } = useContext(Context)
  useEffect(() => {
    //      
    if (level === '    ') {
        const progressBarWidth = 0 + '%';
        setProgressWidth(progressBarWidth);   
    }
    else{
        const progressBarWidth = (level/16)*100 + '%';
        setProgressWidth(progressBarWidth);
    }
    
  }, [level]);

  return (
    <Link to={level != '    ' && ROOMS_ROUTE}  className="progress-bar">
        <div className='swimTextBar'>
            {
                level === '    '?
                level
                :
                text
            }
        </div>
      <div className="progress" style={{ width: progressWidth }}>
        
      </div>
      {
        level === '    '?
        ''
        :
        <div className="progress-text-people">  : {level}/16</div>
      }
      {
        level === '    '?
        ''
        :
        <div className="progress-text-lvl"> : {user.User.roomlvl}</div>
      }
    </Link>
  );
};

export default ProgressBar;
